import { useEffect, useState } from "react";
import { FaHome } from "react-icons/fa";
import { getMoneyInWallet, getTotalTransaction, getTrxGroupByMonth } from "../../api";
import Container from "../../containers/Container";
import Greeting from "./components/Greeting";
import TotalMoney from "./components/TotalMoney";
import TransactionByWallet from "./components/TransactionByWallet";
import TransactionHistory from "./components/TransactionHistory";

const Home = (props) => {
  const [totalTrx, setTotalTrx] = useState({});
  const [moneyInWallet, setMoneyInWallet] = useState([]);
  const [trxByMonths, setTrxByMonths] = useState([]);
  const [loading, setLoading] = useState({totalTrx: true, moneyInWallet: true, trxByMonths: true});

  const loadData = async () => {
    try {
      const res1 = await getTotalTransaction();
      setTotalTrx(res1.data);
      setLoading({totalTrx: false, moneyInWallet: true, trxByMonths: true});
      const res2 = await getMoneyInWallet();
      setMoneyInWallet(res2.data);
      setLoading({moneyInWallet: false, totalTrx: false, trxByMonths: true});
      const res3 = await getTrxGroupByMonth();
      setTrxByMonths(res3.data);
      setLoading({moneyInWallet: false, totalTrx: false, trxByMonths: false});
    } catch (err) {}
    setLoading({totalTrx: false, moneyInWallet: false})
  }

  useEffect(() => {
    loadData();
  }, []);

  return (
    <>
      <Container icon={FaHome} title="Home">
        <div className="main-content">
          <Greeting />
          <TotalMoney total={totalTrx} loading={loading.totalTrx} />
          <TransactionByWallet moneys={moneyInWallet} loading={loading.moneyInWallet} />
          <TransactionHistory transactions={trxByMonths} loading={loading.trxByMonths} {...props} />
        </div>
      </Container>
    </>
  );
}

export default Home;