import { FaShoppingCart } from "react-icons/fa";
import Container from "../../containers/Container";
import FabPlus from "../../components/FabPlus";
import Dialog from "../../components/Dialog";
import EmptyState from "../../components/EmptyState";
import TransactionCard from "./components/TransactionCard";
import { deleteTransaction, getTransactions } from "../../api";
import { useEffect, useState } from "react";
import SkeletonLoading from "./components/SkeletonLoading";
import { MdKeyboardArrowRight, MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";

const Transaction = (props) => {
  const [transactions, setTransactions] = useState([]);
  const [loadingApi, setLoadingApi] = useState(true);
  const [dialog, setDialog] = useState(false);
  const [btnLoading, setBtnLoading] = useState(false);
  const [trxId, setTrxId] = useState("");
  const trxWallet = JSON.parse(localStorage.getItem("trxWallet")) || {};
  const [trxType, setTrxType] = useState(null);
  const [lastPage, setLastPage] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [loadingMore, setLoadingMore] =useState(false);

  const loadTrx = async () => {
    setLoadingApi(true);
    try {
      const res = await getTransactions();
      if(trxWallet.id) setTransactions(res.data.content.filter(d => d.wallet.id === trxWallet.id));
      else setTransactions(res.data.content);
      setLastPage(res.data.last);
      setCurrentPage(res.data.currentPage);
    } catch (err){}
    setLoadingApi(false);
  }

  const handleDelete = async () => {
    setBtnLoading(true);
    try {
      await deleteTransaction(trxId);
      setDialog(false);
      await loadTrx();
    } catch (err){}
    setBtnLoading(false);
  }

  const loadMore = async () => {
    setLoadingMore(true);
    try {
      const res = await getTransactions(currentPage + 1);
      if(trxWallet.id) setTransactions([...transactions, ...res.data.content.filter(d => d.wallet.id === trxWallet.id)]);
      else setTransactions([...transactions, ...res.data.content]);
      setLastPage(res.data.last);
      setCurrentPage(res.data.currentPage);
    } catch (err){}
    setLoadingMore(false);
  }

  const handleTrxChange = (trx) => {
    setTrxType(prevValue => {
      if(prevValue === trx) return null;
      else return trx;
    });
  }
  
  useEffect(() => {
    localStorage.removeItem("walletId");
    localStorage.removeItem("walletName");
    const loadData = async () => {
      setLoadingApi(true);
      try {
        const res = await getTransactions();
        if(trxWallet.id) setTransactions(res.data.content.filter(d => d.wallet.id === trxWallet.id));
        else setTransactions(res.data.content);
        setLastPage(res.data.last);
        setCurrentPage(res.data.currentPage);
      } catch (err){}
      setLoadingApi(false);
    }
    loadData();
  }, [trxWallet.id]);

  return (
    <Container icon={FaShoppingCart} title="Transaction">
      <div className="main-content">
          <div 
            className="form-control form-control-sm cursor-pointer align-items-center d-flex justify-content-between" 
            onClick={() => props.history.push("/wallet?type=select")}
          >
            <span>{trxWallet.name || "All Wallet"}</span>
            <MdKeyboardArrowRight />
          </div>
        <hr className="my-2"/>
        <div className="d-flex mb-3">
            <div className="p-2 bg-light d-flex align-items-center shadow-sm cursor-pointer text-dark mr-2" onClick={() => handleTrxChange(0)} >
              {trxType === 0 ? 
                <MdRadioButtonChecked /> :
              <MdRadioButtonUnchecked />}
              <small style={{fontSize: '.8em'}} className="ml-2">Incoming Transaction</small>
            </div>
            <div className="p-2 bg-light d-flex align-items-center shadow-sm cursor-pointer text-dark" onClick={() => handleTrxChange(1)} >
              {trxType === 1 ?
                <MdRadioButtonChecked /> :
              <MdRadioButtonUnchecked />}
              <small style={{fontSize: '.8em'}} className="ml-2">Outcoming Transaction</small>
            </div>
          </div>
        {loadingApi ? [1,2].map(i => <SkeletonLoading key={i} />) : 
          transactions.length === 0 ? <EmptyState /> : 
          trxType !== null ? transactions.filter(t => t.type === trxType).length === 0 ? <EmptyState /> :
          transactions.filter(t => t.type === trxType).map(transaction => (
            <TransactionCard 
              {...props} 
              key={transaction.id} 
              transaction={transaction} 
              onDelete={() => {setDialog(true); setTrxId(transaction.id)}}
              onEdit={() => props.history.push(`/transaction/edit?id=${transaction.id}`)}
            />
          )) : transactions.map(transaction => (
            <TransactionCard 
              {...props} 
              key={transaction.id} 
              transaction={transaction} 
              onDelete={() => {setDialog(true); setTrxId(transaction.id)}}
              onEdit={() => props.history.push(`/transaction/edit?id=${transaction.id}`)}
            />
        ))}
        {!lastPage && !loadingApi && !loadingMore &&
          <div className="d-flex justify-content-center mb-3">
              <button className="btn btn-outline-secondary btn-sm" onClick={loadMore}>Load More</button>
          </div> 
        }
        {loadingMore && [1,2].map(i => <SkeletonLoading key={i} />)}
      </div>
      <FabPlus 
        onClick={() => props.history.push("/transaction/create")}
      />
      <Dialog 
        show={dialog}
        onNegative={() => setDialog(false)}
        onPositive={handleDelete}
        loading={btnLoading}
      />
    </Container>
  )
}

export default Transaction;