import { FaShoppingCart } from "react-icons/fa";
import { MdKeyboardArrowRight } from "react-icons/md";
import Skeleton from "react-loading-skeleton";
import NumberFormat from "react-number-format";

const TransactionHistory = (props) => {
  const { transactions, loading } = props;
  const date = new Date();
  const year = localStorage.getItem("trxYear") || date.getFullYear();
  const months = [
    "",
    "Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Aug", "Sep", "Okt", "Nov", "Des"
  ];

  const numberFormat = (txt) => {
    return (
      <NumberFormat
        style={{whiteSpace: 'nowrap'}}
        value={txt}
        displayType={"text"}
        thousandSeparator={true}
        renderText={value => value === "0" ? "-" : value}
      />
    )
  }

  return (
    <div className="mt-3 mb-4 shadow-sm bg-light">
      <div 
        className="px-2 py-2 pb-1 d-flex text-dark justify-content-between align-items-center cursor-pointer"
         onClick={() => props.history.push('/year-lists')}>
        <span>
          <FaShoppingCart className="mr-1 align-self-center" size="0.85em" />
          <span style={{fontSize: '0.95em'}}>Transaction History ({year})</span>
        </span>
        <MdKeyboardArrowRight />
      </div>
      <div className="pt-0 px-2 pb-2">
        <table className="table table-sm">
          <thead>
            <tr>
              <td width="10%"><small style={{fontSize: 12}}><strong>Month</strong></small></td>
              <td width="25%" align="right"><small style={{fontSize: 12}}><strong>In</strong></small></td>
              <td width="25%" align="left"><small style={{fontSize: 12}}><strong>Out</strong></small></td>
              <td width="20%" align="right"><small style={{fontSize: 12}}><strong>Total</strong></small></td>
            </tr>
          </thead>
          <tbody>
            {loading ? [1,2,3,4,5,6].map(i => (
              <tr key={i}>
                <td colSpan="4"> <Skeleton width={"100%"} /> </td>
              </tr>
            )) :transactions.map(trx => (
              <tr className="cursor-pointer" key={trx.month} onClick={() => props.history.push(`/transaction/by-month?month=${trx.month}`)}>
                <td><small style={{fontSize: 11}}>{months[parseInt(trx.month)]}</small></td>
                <td align="right"><small style={{fontSize: 11}}>{numberFormat(trx.in)}</small></td>
                <td align="left"><small style={{fontSize: 11}}>{numberFormat(trx.out)}</small></td>
                <td align="right"><small style={{fontSize: 11}}>{numberFormat(trx.saldo)}</small></td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}

export default TransactionHistory;