import qs from "qs";
import { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import { getWallets } from "../../api";
import WalletList from "./WalletList";
import WalletSelect from "./WalletSelect";

const Wallet = (props) => {
  const wallet = qs.parse(props.location.search, {ignoreQueryPrefix: true});
  const [data, setData] = useState([]);
  const [loadingApi, setLoadingApi] = useState(true);
  const [lastPage, setLastPage] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [loadingMore, setLoadingMore] =useState(false);

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const res = await getWallets();
      setData(res.data.content);
      setLastPage(res.data.last);
      setCurrentPage(res.data.currentPage);
    } catch (err){}
    setLoadingApi(false);
  }

  const onLoadMore = async () => {
    setLoadingMore(true);
    try {
      const res = await getWallets(currentPage + 1);
      setData([...data, ...res.data.content]);
      setLastPage(res.data.last);
      setCurrentPage(res.data.currentPage);
    } catch (err){}
    setLoadingMore(false);
  }

  useEffect(() => loadData(), []);

  if(wallet.type === "list") {
    return <WalletList {...props} wallets={data} loadingApi={loadingApi} loadingMore={loadingMore} onLoadMore={onLoadMore} lastPage={lastPage} />
  } else if (wallet.type === "select") {
    return <WalletSelect {...props} wallets={data} loadingApi={loadingApi} loadingMore={loadingMore} onLoadMore={onLoadMore} lastPage={lastPage} />
  } else return <Redirect to="/" />
}

export default Wallet;