import { useEffect, useState } from "react";
import { getYears } from "../../api";
import NavTop from "../../components/NavTop";
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import Skeleton from "react-loading-skeleton";

const YearList = (props) => {
  const [years, setYears] = useState([]);
  const date = new Date();
  const year = date.getFullYear()
  const trxYear = localStorage.getItem("trxYear") || year;
  const [loadingApi, setLoadingApi] = useState(true);

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const res = await getYears();
      setYears(res.data);
    } catch (err) {}
    setLoadingApi(false);
  }

  const handleCheck = (y) => {
    localStorage.setItem("trxYear", y);
    window.history.back();
  }

  useEffect(() => {
    loadData();
  }, []);

  return (
    <>
      <NavTop title="Pilih Tahun" />
      <div className="main-content">
        <div className="row">
          {loadingApi ? [1,2,3,4,5,6].map(i => (
            <div key={i} className="col-6 mb-3 text-dark">
              <Skeleton width={"100%"} height={37.5} />
            </div>
          )) : years.map(y => (
            <div key={y.year} className="col-6 mb-3 text-dark">
              <div className="p-2 bg-light d-flex align-items-center" onClick={() => handleCheck(y.year)}>
                {y?.year?.toString() === trxYear?.toString() ? 
                  <MdRadioButtonChecked /> : 
                  <MdRadioButtonUnchecked />
                }
                <span style={{marginBottom: 1}} className="ml-2">{y.year}</span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  )
}

export default YearList;