import { useState } from "react";
import NavTop from "../../components/NavTop";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";
import { changePassword } from "../../api";
import { toast } from "react-toastify";

const ChangePassword = (props) => {
  const userdata = JSON.parse(localStorage.getItem("userdata"));
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newPasswordRepeat, setNewPasswordRepeat] = useState("");
  const [errors, setErrors] = useState({});
  const [btnLoading, setBtnLoading] = useState(false);
  
  const fields = [
    {label: "Password Lama", value: oldPassword, onChange: (e) => setOldPassword(e.target.value), error: errors?.old_password, type: "password"},
    {label: "Password Baru", value: newPassword, onChange: (e) => setNewPassword(e.target.value), error: errors?.new_password, type: "password"},
    {label: "Konfirmasi Password Baru", value: newPasswordRepeat, onChange: (e) => setNewPasswordRepeat(e.target.value), error: errors?.new_password_repeat, type: "password"},
  ];

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      const res = await changePassword({old_password: oldPassword, new_password: newPassword, new_password_repeat: newPasswordRepeat, id: userdata.id});
      toast.success(res.data.message);
      window.history.back();
    } catch (err){
      setErrors(err?.response?.data);
      setBtnLoading(false);
    }
  }

  return (
    <>
      <NavTop title="Ganti Password" />
      <div className="main-content">
        {fields.map((field, i) => (
          <FormField 
            label={field.label}
            value={field.value}
            onChange={field.onChange}
            error={field.error}
            type={field.type}
            key={i}
          />
        ))}
        <div className="form-group">
        {btnLoading ? 
          <LoadingButton />
        : <button className="btn btn-primary btn-sm btn-block" onClick={handleSubmit}>Simpan</button>
        }
      </div>
      </div>
    </>
  )
}

export default ChangePassword;