import NavTop from "../../components/NavTop";
import FabPlus from "../../components/FabPlus";
import CardSelect from "./components/CardSelect";
import SkeletonLoading from "./components/SkeletonLoading";
import EmptyState from "../../components/EmptyState";
import { useState } from "react";

const WalletSelect = (props) => {
  const { wallets, loadingApi, loadingMore, onLoadMore, lastPage } = props;
  const [keyword, setKeyword] = useState("");
  const onLiveSearch = (text) => {
    let searchTimeout;
    if(searchTimeout !== undefined) clearTimeout(searchTimeout);
    searchTimeout = setTimeout(() => setKeyword(text), 500);
  }

  return (
    <>
      <NavTop title="Select Wallet" />
        <div className="main-content">
          <input 
            type="search" 
            className="form-control form-control-sm" 
            placeholder="search..."
            onChange={e => onLiveSearch(e.target.value)}
          />
          <hr className="my-2"/>
          {loadingApi ? [1,2,3,4,5].map(i => <SkeletonLoading key={i} />) : 
            wallets.length === 0 ? <EmptyState /> :
            keyword ? wallets.filter(w => w?.name?.toLowerCase().indexOf(keyword.toLowerCase()) > -1).map(wallet => (
              <CardSelect key={wallet.id} {...props} wallet={wallet} />
            )) :
            wallets.map(wallet => (
            <CardSelect key={wallet.id} {...props} wallet={wallet} />
          ))}
          {!lastPage && !loadingApi && !loadingMore &&
            <div className="d-flex justify-content-center mb-5">
                <button className="btn btn-outline-secondary btn-sm" onClick={onLoadMore}>Load More</button>
            </div> 
          }
          {loadingMore && [1,2,3,4,5].map(i => <SkeletonLoading key={i} />)}
        </div>
        <FabPlus 
          onClick={() => props.history.push("/wallet/create")}
        />
    </>
  )
}

export default WalletSelect;