const LoadingState = (props) => {
  const { loading } = props;
  const className = loading ? "loading-bg" : "d-none";

  return (
    <div className={className}>
      <div className="loading-content d-flex align-items-center">
        <div className="spinner-grow text-primary" role="status" style={{width: '2.5em',  height: '2.5em'}}>
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    </div>
  )
}

export default LoadingState;