import instance from "./instance";

export async function createWallet(data) {
  return await instance.post("wallets", data);
}

export async function updateWallet(data) {
  return await instance.put("wallets", data);
}

export async function getWallets(page = 1) {
  return await instance.get("wallets?page=" + page);
}

export async function getMoneyInWallet() {
  return await instance.get("wallets/total-money");
}

export async function getWallet(id) {
  return await instance.get(`wallets/${id}`);
}

export async function deleteWallet(id) {
  return await instance.delete(`wallets/${id}`);
}