import instance from "./instance";

export async function userVerification(data) {
  return await instance.post("user/verification", data);
}

export async function resetMyPassword(data) {
  return await instance.put("user/reset-my-password", data);
}

export async function renewMyPassword(data) {
  return await instance.put("user/renew-my-password", data);
}

export async function updateProfile(data) {
  return await instance.put("user/me", data);
}

export async function changePassword(data) {
  return await instance.put("user/change-password", data);
}