import instance from "./instance";

export async function createTransaction(data) {
  return await instance.post("transactions", data);
}

export async function updateTransaction(data) {
  return await instance.put("transactions", data);
}

export async function getTransactions(page = 1) {
  return await instance.get("transactions?page=" + page);
}

export async function getTransaction(id) {
  return await instance.get(`transactions/${id}`);
}

export async function getYears() {
  return await instance.get(`transactions/years`);
}

export async function getTotalTransaction() {
  return await instance.get(`transactions/total-transaction`);
}

export async function deleteTransaction(id) {
  return await instance.delete(`transactions/${id}`);
}

export async function getTrxGroupByMonth() {
  const date = new Date();
  const year = localStorage.getItem("trxYear") || date.getFullYear();
  return await instance.get(`transactions/transaction-group-by-month?year=${year}`);
}

export async function getTrxByMonth(month) {
  const date = new Date();
  const year = localStorage.getItem("trxYear") || date.getFullYear();
  return await instance.get(`transactions/by-month/${year}?month=${month}`);
}