import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";

const CardList = (props) => {
  const { wallet } = props;
  const walletId = localStorage.getItem("walletId");
  const trxWallet = JSON.parse(localStorage.getItem("trxWallet")) || {};

  const selectWallet = (wallet) => {
    if(wallet.id === trxWallet.id) localStorage.removeItem("trxWallet");
    else localStorage.setItem("trxWallet", JSON.stringify(wallet));
    localStorage.setItem("walletId", wallet.id);
    localStorage.setItem("walletName", wallet.name);
    window.history.back();
  }

  return (
    <div 
      className="bg-light p-3 shadow-sm d-flex cursor-pointer mb-2" 
      onClick={() => selectWallet(wallet)}
    >
      <div className="d-flex align-items-center text-secondary mr-3">
        {walletId === wallet.id || trxWallet?.id === wallet.id? <MdRadioButtonChecked /> :
          <MdRadioButtonUnchecked/>
        }
      </div>
      <div>
        <span className="d-block text-dark" style={{fontSize: '.95em'}}>{wallet.name}</span>
        <small className="d-block text-secondary" style={{fontSize: '.85em'}}>{wallet.description}</small>
      </div>
    </div>
  )
}

export default CardList;