import { useEffect, useState } from "react";
import { getTrxByMonth } from "../../api";
import NavTop from "../../components/NavTop";
import Skeleton from "react-loading-skeleton";
import NumberFormat from "react-number-format";
import qs from "qs";

const TrxByMonth = (props) => {
  const param = qs.parse(props.location.search, {ignoreQueryPrefix: true});
  const month = param.month;
  const [transactions, setTransactions] = useState([]);
  const [loading, setLoading] = useState(true);
  const months = [
    "",
    "Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Aug", "Sep", "Okt", "Nov", "Des"
  ];

  const Months = [
    "",
    "Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"
  ];

  const showMonth = (date) => {
    date = date.split("-")
    return date[2] + " " + months[parseInt(date[1])] + " " + date[0];
  }

  const numberFormat = (txt) => {
    return (
      <NumberFormat
        style={{whiteSpace: 'nowrap'}}
        value={txt}
        displayType={"text"}
        thousandSeparator={true}
        renderText={value => value === "0" ? "-" : value}
      />
    )
  }

  useEffect(() => {
    const loadData = async () => {
      setLoading(true);
      try {
        const res = await getTrxByMonth(month);
        setTransactions(res.data);
      } catch (err){}
      setLoading(false);
    }
    loadData();
  }, [month]);

  return (
    <>
      <NavTop title={"Transaksi Bulan " + Months[month]}/>
      <div className="main-content">
      <table className="table table-sm">
          <thead>
            <tr>
              <td width="25%" align="right"><small style={{fontSize: 12}}><strong>Date</strong></small></td>
              <td width="25%" align="right"><small style={{fontSize: 12}}><strong>In</strong></small></td>
              <td width="25%" align="left"><small style={{fontSize: 12}}><strong>Out</strong></small></td>
              <td width="20%" align="right"><small style={{fontSize: 12}}><strong>Saldo</strong></small></td>
            </tr>
          </thead>
          <tbody>
            {loading ? [1,2,3,4,5,6].map(i => (
              <tr key={i}>
                <td colSpan="4"> <Skeleton width={"100%"} /> </td>
              </tr>
            )) :transactions.map((trx, i) => (
              <tr key={i}>
                <td align="right"><small style={{fontSize: 11}}>{showMonth(trx.date)}</small></td>
                <td align="right"><small style={{fontSize: 11}}>{numberFormat(trx.in)}</small></td>
                <td align="left"><small style={{fontSize: 11}}>{numberFormat(trx.out)}</small></td>
                <td align="right"><small style={{fontSize: 11}}>{numberFormat(trx.saldo)}</small></td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  )
}

export default TrxByMonth;