import { useEffect, useState } from "react";

const Greeting = (props) => {
  const [greeting, setGreeting] = useState("");
  const userdata = JSON.parse(localStorage.getItem("userdata"));

  useEffect(() => {
    let dateToday = new Date();
    const timeNow = dateToday.getTime();
    dateToday.setTime(timeNow);
    const hour = dateToday.getHours();
    if(hour > 18) setGreeting("Evening");
    else if(hour > 12) setGreeting("Afternoon");
    else setGreeting("Morning");
  }, []);
  
  return (
    <div className="py-3 px-2 mb-3 shadow-sm bg-light">
      <small>Good {greeting}! <span className="text-primary font-weight-bold">{userdata.name}</span>!</small>
    </div>
  )
}

export default Greeting;