import { useState } from "react";
import NavTop from "../../components/NavTop";
import FormField from "../../components/FormField";
import LoadingButton from "../../components/LoadingButton";
import { updateProfile } from "../../api";

const EditProfile = (props) => {
  const userdata = JSON.parse(localStorage.getItem("userdata"));
  const [name, setName] = useState(userdata.name || "");
  const [username, setUsername] = useState(userdata.username || "");
  const [phone, setPhone] = useState(userdata.phone || "");
  const [errors, setErrors] = useState({});
  const [btnLoading, setBtnLoading] = useState(false);
  
  const fields = [
    {label: "Nama", value: name, onChange: (e) => setName(e.target.value), error: errors?.name, type: "text"},
    {label: "Username", value: username, onChange: (e) => setUsername(e.target.value), error: errors?.username, type: "text"},
    {label: "No HP", value: phone, onChange: (e) => setPhone(e.target.value), error: errors?.phone, type: "number"},
  ];

  const handleSubmit = async () => {
    setBtnLoading(true);
    try {
      const res = await updateProfile({name, phone, username, id: userdata.id});
      localStorage.setItem("userdata", JSON.stringify(res.data));
      window.history.back();
    } catch (err){
      setErrors(err?.response?.data);
      setBtnLoading(false);
    }
  }

  return (
    <>
      <NavTop title="Edit Profile" />
      <div className="main-content">
        {fields.map((field, i) => (
          <FormField 
            label={field.label}
            value={field.value}
            onChange={field.onChange}
            error={field.error}
            type={field.type}
            key={i}
          />
        ))}
        <div className="form-group">
        {btnLoading ? 
          <LoadingButton />
        : <button className="btn btn-primary btn-sm btn-block" onClick={handleSubmit}>Simpan</button>
        }
      </div>
      </div>
    </>
  )
}

export default EditProfile;